.wrapper{
  .field:not(.textarea-selector){
    position: relative;
    height: 100px;
    &.is-grouped{
      top: 50px;
    }
  }
  .textarea-selector{
    position: relative;
    .error-msg{
      bottom: -30px;
    }
  }
  .error-msg{
    margin-bottom: 10px;
    position: absolute;
    bottom: -4px;
  }
  min-width: 600px;
  .succes-bar{
    max-width: 600px;
    margin: 0 auto;
  }
  .main-form-container{
    display: grid;
    place-items: center;
    max-width: 600px;
    min-height: 710px;

    margin: 0 auto;
    border: 1px solid rgba(0,0,0,0.1);
    padding: 35px 5px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.2);
    background-color: white;
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    input[type=number] {
      -moz-appearance: textfield;
    }


    .flags-container{
      .active-lang{
        border: 2px solid rgba(0,0,0,0.75) !important;
        box-shadow: 0 4px 6px rgba(0,0,0,0.1);
      }
      .inactive-lang{
        opacity: 0.3;
      }
      .flag-icon{
        height: 50px;
        width: 56px;
        padding: 1px 6px;
        border: 2px solid transparent;
        transition: all 0.25s ease-in-out;
        cursor: pointer;
      }
    }

    .form-wrapper{
      width: 90%;
      max-width: 520px;
    }
  }
}
